/* Base Styles */
.certifications-section {
  padding: 50px 0;
  background-color: #f4f4f4;
  text-align: center;
}

.certifications-heading {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 30px;
}

.certifications-container {
  display: flex;
  justify-content: center; /* Centers the certificates horizontally */
  align-items: center; /* Aligns items vertically in the center */
  gap: 30px; /* Adds space between each certificate */
  flex-wrap: wrap; /* Allows certificates to wrap when space is tight */
  overflow-x: auto; /* Enables horizontal scrolling if needed */
  padding-bottom: 20px; /* Adds space at the bottom to avoid scroll cutoff */
}

.certification-item {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 250px;
  transition: transform 0.3s ease-in-out;
  text-align: center;
}

.certification-item:hover {
  transform: translateY(-10px);
}

.certification-item h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 15px;
}

.download-btn {
  background-color: #2d87f0;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.download-btn:hover {
  background-color: #1f6fc7;
}

/* Media Queries */

/* For Tablets and smaller screens (max-width: 768px) */
@media (max-width: 768px) {
  .certifications-container {
    gap: 20px; /* Reduced gap for smaller screens */
  }

  .certification-item {
    width: 45%; /* 2 certificates per row on tablet screens */
  }
}

/* For Mobile Devices (max-width: 480px) */
@media (max-width: 480px) {
  .certifications-container {
    gap: 15px; /* Reduced gap further for smaller screens */
  }

  .certification-item {
    width: 90%; /* 1 certificate per row on mobile */
  }

  .download-btn {
    padding: 12px 25px; /* Larger buttons on mobile */
    font-size: 1.1rem;
  }
}
